.header {
  position: relative;
  min-height: 15vh;
  top: 0px;
}

.header.expand > .transition {
  -webkit-transform: translateX(45vw);
          transform: translateX(45vw);
}

.header h1, .header span {
  color: #FF5D48;
  color: var(--color-main)
}

.heading {
  display: inline-block;
  width: -webkit-max-content;
  width: max-content;
  transition: 0.5s;
  cursor: pointer;
  text-align: right;
}

.heading > .transition {
  display: inline-block;
  -webkit-transform-origin: 35% 50%;
          transform-origin: 35% 50%;
  transition: 0.5s;
}

.heading.expand {
  margin-right: 90%;
}

.heading.expand:hover .transition {
  transform: translateX(50%) rotate(90deg);
  -webkit-transform: translateX(30%) rotate(90deg);
  -moz-transform: translateX(50%) rotate(90deg);
  -ms-transform: translateX(50%) rotate(90deg);
  -o-transform: translateX(50%) rotate(90deg);
}

.spotify {
  display: inline-block;
  position: absolute;
  font-weight: bold;
  font-size: 1.25em;
  text-align: right;
  right: 25px;

  -webkit-transform: translateY(235%);

          transform: translateY(235%);
}

.spotify span, .spotify a {
  transition: 0.5s all;
}

.spotify a {
  color: black;
  text-decoration: none;
}

.spotify a:hover span {
  color: #1ED760;
}

.main {
  transition: 1s all;
  max-width: 50%;
  margin: auto;
}

.main.expand {
  -webkit-transform: translateY(250%);
          transform: translateY(250%);
  max-width: 75%;
  border-radius: 20em;
}

